import axios from 'axios'

class YaTranslator {
    constructor(key) {
        this.setKey(key);
        this.url = "http://localhost/8081/translate/getjob";
    }
    getPromise(text) {
        if (!this.key) {
            return new Promise((resolve) => {
                resolve();
            });
        }
        
        const request = this.url;
        
        return axios.get(request);
    }
    setKey(key) {
        if (!key) {
            this.key = "";
            return;
        }
        this.key = "key=" + key;
    }
}

export default YaTranslator;


